"use client";

import { useState, useRef } from "react";
import Link from "next/link";
import { ChevronLeft, Upload, Loader2, Image as ImageIcon } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { useToast } from "@/hooks/use-toast";
import Image from "next/image";

export default function EbookAddPage() {
  const { toast } = useToast();
  const [title, setTitle] = useState("");
  const [author, setAuthor] = useState("");
  const [price, setPrice] = useState(0);
  const [description, setDescription] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [imageUrl, setImageUrl] = useState("");
  const [imagePreview, setImagePreview] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);


  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    // In a real application, you would make an API call here
    // to add the new ebook data to your database, including the uploaded file.
    console.log("New Ebook Data:", {
      title,
      author,
      price,
      description,
      imageUrl, // Or the file data if uploaded
    });

    setTimeout(() => {
      toast({
        title: "Ebook Added",
        description: `"${title}" has been successfully added.`,
      });
      setIsLoading(false);
      // Optionally reset form or redirect
    }, 1000);
  };
  
  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
        setImageUrl(""); // Clear URL input if a file is uploaded
      };
      reader.readAsDataURL(file);
    }
  };

  const displaySrc = imagePreview || imageUrl;


  return (
    <div className="mx-auto grid max-w-[59rem] flex-1 auto-rows-max gap-4">
      <div className="flex items-center gap-4">
        <Button variant="outline" size="icon" className="h-7 w-7" asChild>
          <Link href="/admin">
            <ChevronLeft className="h-4 w-4" />
            <span className="sr-only">Back</span>
          </Link>
        </Button>
        <h1 className="flex-1 shrink-0 whitespace-nowrap text-xl font-semibold tracking-tight sm:grow-0">
          Add New eBook
        </h1>
        <Badge variant="outline" className="ml-auto sm:ml-0">
          New
        </Badge>
        <div className="hidden items-center gap-2 md:ml-auto md:flex">
          <Button variant="outline" size="sm" asChild>
            <Link href="/admin">Discard</Link>
          </Button>
          <Button size="sm" onClick={handleSubmit} disabled={isLoading}>
            {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Save eBook
          </Button>
        </div>
      </div>
      <form
        onSubmit={handleSubmit}
        className="grid gap-4 md:grid-cols-[1fr_250px] lg:grid-cols-3 lg:gap-8"
      >
        <div className="grid auto-rows-max items-start gap-4 lg:col-span-2 lg:gap-8">
          <Card>
            <CardHeader>
              <CardTitle>Ebook Details</CardTitle>
              <CardDescription>
                Fill in the details for the new ebook.
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid gap-6">
                <div className="grid gap-3">
                  <Label htmlFor="title">Title</Label>
                  <Input
                    id="title"
                    type="text"
                    className="w-full"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    placeholder="The Art of Hacking"
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="author">Author</Label>
                  <Input
                    id="author"
                    type="text"
                    className="w-full"
                    value={author}
                    onChange={(e) => setAuthor(e.target.value)}
                    placeholder="John Doe"
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="description">Description</Label>
                  <Textarea
                    id="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    className="min-h-32"
                    placeholder="A book about..."
                  />
                </div>
                <div className="grid gap-3">
                  <Label htmlFor="price">Price (GH₵)</Label>
                  <Input
                    id="price"
                    type="number"
                    value={price}
                    onChange={(e) => setPrice(parseFloat(e.target.value))}
                  />
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
        <div className="grid auto-rows-max items-start gap-4 lg:gap-8">
          <Card>
            <CardHeader>
              <CardTitle>Cover Image</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-2">
                <div className="relative aspect-[2/3] w-full rounded-md overflow-hidden border flex items-center justify-center bg-muted">
                  {displaySrc ? (
                    <Image
                      alt="Product image"
                      className="object-cover"
                      fill
                      src={displaySrc}
                      sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                    />
                  ) : (
                    <div className="text-center text-muted-foreground">
                      <ImageIcon className="h-8 w-8 mx-auto" />
                      <p className="mt-2 text-sm">No image</p>
                    </div>
                  )}
                </div>
                 <div className="grid gap-3">
                  <Label htmlFor="image-url">Image URL</Label>
                  <Input
                    id="image-url"
                    type="text"
                    className="w-full"
                    value={imageUrl}
                    onChange={(e) => {
                      setImageUrl(e.target.value);
                      setImagePreview(null);
                    }}
                    placeholder="https://example.com/image.png"
                  />
                </div>
                <Input
                  id="cover-image-upload"
                  type="file"
                  className="hidden"
                  ref={fileInputRef}
                  onChange={handleImageUpload}
                  accept="image/*"
                />
                <Button variant="outline" className="w-full" onClick={() => fileInputRef.current?.click()}>
                  <Upload className="h-4 w-4" />
                  <span className="ml-2">Upload from Computer</span>
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      </form>
      <div className="flex items-center justify-center gap-2 md:hidden">
        <Button variant="outline" size="sm" asChild>
            <Link href="/admin">Discard</Link>
        </Button>
        <Button size="sm" onClick={handleSubmit} disabled={isLoading}>
          {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
          Save eBook
        </Button>
      </div>
    </div>
  );
}
